<template>
  <div class="studentList">
    <!-- 查询、重置 -->
    <el-form
      :inline="true"
      :model="formInline"
      class="demo-form-inline"
      size="small"
    >
      <el-form-item label="姓名">
        <el-input
          v-model="formInline.name"
          placeholder="请输入姓名查询"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="find">查询</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="reset">重置</el-button>
      </el-form-item>
    </el-form>
    <!-- 表格加载数据实现 -->
    <el-table :data="compData" border style="width: 100%">
      <el-table-column prop="name" label="姓名" align="center">
      </el-table-column>
      <el-table-column prop="sex_text" label="性别" align="center">
      </el-table-column>
      <el-table-column prop="age" label="年龄" align="center">
      </el-table-column>
      <el-table-column prop="number" label="学号" align="center">
      </el-table-column>
      <el-table-column prop="class" label="班级" align="center">
      </el-table-column>
      <el-table-column prop="state_text" label="状态" align="center">
      </el-table-column>
      <el-table-column prop="address" label="地址" align="center">
      </el-table-column>
      <el-table-column prop="phone" label="联系方式" align="center">
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            @click="del(scope.row)"
            type="danger"
            size="mini"
            icon="el-icon-delete"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页实现 -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20, 30, 50]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
import { students, studentDel } from "@/api/api.js";
export default {
  data() {
    return {
      tableData: [],
      currentPage: 1, // 当前页数
      pageSize: 5, // 每页显示的条数
      total: 0, // 总条数
      formInline: {
        name: "",
      },
    };
  },
  created() {
    this.getData();
  },
  computed: {
    compData() {
      // 显示页面数据公式：slice((currentPage - 1) * pageSize, currentPage * pageSize)
      // 公式：从第(currentPage - 1) * pageSize条开始，读取到第 currentPage * pageSize -1 条
      return this.tableData.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      );
    },
  },
  methods: {
    getData(params) {
      console.log("" === undefined);
      console.log("获取数据");
      students(params).then((res) => {
        if (res.data.status === 200) {
          console.log("res:", res.data.data);
          this.total = res.data.total; // 获取数据总条数
          this.tableData = res.data.data;
          this.tableData.forEach((item) => {
            // 尽量不要修改原数据
            item.sex === 1 ? (item.sex_text = "男") : (item.sex_text = "女");
            item.state === "1"
              ? (item.state_text = "已入学")
              : item.state === "2"
              ? (item.state_text = "未入学")
              : (item.state_text = "休学中");
          });
        }
      });
    },
    handleSizeChange(val) {
      this.pageSize = val; // 修改每页显示的条数
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val; // 修改当前页数
      console.log(`当前页: ${val}`);
    },
    del(row) {
      console.log(row);
      studentDel(row.id).then((res) => {
        if (res.data.status === 200) {
          this.$message({ message: "删除数据成功", type: "success" });
          console.log("刷新数据");
          this.getData();
        }
      });
    },
    // 查询
    find() {
      console.log(this.formInline);
      this.getData(this.formInline); // 带查询条件的请求
    },
    // 重置
    reset() {
      console.log(this.formInline);
      this.formInline = {};
      this.getData(this.formInline);
    },
  },
};
</script>

<style lang="scss">
.studentList {
  .demo-form-inline,
  .el-form-item {
    text-align: left;
  }

  .el-pagination {
    margin-top: 20px;
    text-align: left;
  }
}
</style>