import service from "@/utils/service.js"

// 登录接口
export function login(data) {
  // return service.post("/login", data)
  return service({
    url: "/login",
    method: "post",
    data
  })
}

// 学生列表查询接口
export function students(params) {
  return service({
    url: "/students",
    method: "get",
    params
  })
}

// 学生列表删除接口
export function studentDel(id) {
  return service({
    url: `/studentDelete/${id}`,
    method: "delete"
  })
}

// 信息列表新增接口
export function info(data) {
  return service({
    url: "/info",
    method: "post",
    data
  })
}

// 信息列表查询接口
export function getInfo() {
  return service({
    url: "/info",
    method: "get",
  })
}

// 信息列表修改接口
export function updateInfo(data) {
  return service({
    url: "/info",
    method: "put",
    data
  })
}

